import React from 'react';

import {ReactComponent as ImtuIcon} from '../../styles/img/br-mtu-intl.svg';
import {ReactComponent as TypoIcon} from '../../styles/img/typography.svg';
import {ReactComponent as IgiftIcon} from '../../styles/img/br-gift-intl.svg';
import {ReactComponent as PaletteIcon} from '../../styles/img/palette.svg';
import {NavLink} from 'react-router-dom';
import NavItem from './NavItem';

export default class Sidebar extends React.Component {
    render() {
        return (
            <nav className="sidebar">
                <div className="sidebar-menu">
                    <ul className="list-unstyled">
                        <li>
                            <NavLink to="/home" className="text-uppercase">Home</NavLink>
                        </li>
                        <li>
                            <NavLink to="/" exact className="text-uppercase">Activate hard card</NavLink>
                        </li>
                        <NavItem className="text-uppercase" text="Story Book">
                            <NavLink to="/typography" className="text-uppercase">
                                <span className="sidebar-menu--icon">
                                    <TypoIcon />
                                </span>
                                <span>Typography</span>
                            </NavLink>
                            <NavLink to="/colors" className="text-uppercase">
                                <span className="sidebar-menu--icon">
                                    <PaletteIcon />
                                </span>
                                <span>Colors</span>
                            </NavLink>
                        </NavItem>
                        <NavItem className="text-uppercase" text="Products">
                            <NavLink to="/imtu" className="text-uppercase">
                                <span className="sidebar-menu--icon">
                                    <ImtuIcon />
                                </span>
                                <span>INTERNATIONAL MOBILE RECHARGES</span>
                            </NavLink>
                            <NavLink to="/igift" className="text-uppercase">
                                <span className="sidebar-menu--icon">
                                    <IgiftIcon />
                                </span>
                                <span>INTERNATIONAL E-GIFT</span>
                            </NavLink>
                        </NavItem>
                        <li className="sidebar-menu--children">
                            <a href="/" className="text-uppercase">Reports</a>
                        </li>
                        <li className="sidebar-menu--children">
                            <a href="/" className="text-uppercase">Tools</a>
                        </li>
                        <li className="sidebar-menu--children">
                            <a href="/" className="text-uppercase">BR University</a>
                        </li>
                        <li>
                            <a href="/" className="text-uppercase">About</a>
                        </li>
                    </ul>
                </div>
            </nav>
        )
    }
}