import React from 'react';
import {ReactComponent as TypoIcon} from '../styles/img/typography.svg';

export default class Typography extends React.Component{
    render(){
        return(
            <div className="container">
                <div className="island px-4 py-3">
                    <h2>
                        <TypoIcon className="mr-2" />
                        Typography
                    </h2>
                    <div className="mb-4">
                        <h1 className="mb-n3">BR University header</h1>
                        <pre className="bg-light p-3 rounded-lg">&lt;h1 class="h1"&gt;BR University header&lt;/h1&gt;</pre>
                    </div>
                    <div className="mb-4">
                        <h2 className="mb-0">Primary header</h2>
                        <pre className="bg-light p-3 rounded-lg">&lt;h2 class="h2"&gt;Primary header&lt;/h2&gt;</pre>
                    </div>
                    <div className="mb-4">
                        <h3 className="mb-2">Sub header, special body text</h3>
                        <pre className="bg-light p-3 rounded-lg">&lt;h3 class="h3"&gt;Sub header, special body text&lt;/h3&gt;</pre>
                    </div>
                    <div className="mb-4">
                        <p className="mb-2 font-size-base">Primary body text, button, dropdown, small input</p>
                        <pre className="bg-light p-3 rounded-lg">&lt;p class="font-size-base"&gt;Primary body text, button, dropdown, small input&lt;/p&gt;</pre>
                    </div>
                    <div className="mb-4">
                        <p className="mb-2 font-size-sm">Description, information, error</p>
                        <pre className="bg-light p-3 rounded-lg">&lt;p class="font-size-sm"&gt;Description, information, error&lt;/p&gt;</pre>
                    </div>
                    <div className="mb-4">
                        <p className="mb-2 font-size-xs">Special information</p>
                        <pre className="bg-light p-3 rounded-lg">&lt;p class="font-size-xs"&gt;Special information&lt;/p&gt;</pre>
                    </div>
                    <div className="mb-4">
                        <header className="mb-2 font-size-xl">Special header</header>
                        <pre className="bg-light p-3 rounded-lg">&lt;header class="font-size-xl"&gt;Special header&lt;/header&gt;</pre>
                    </div>
                </div>
            </div>
        )
    }
}